import { Fragment, useEffect} from 'react'
//import Newsletter from "./Newsletter"
import { Row, Col, CardTitle } from 'reactstrap'
import { useDispatch, useSelector } from 'react-redux'
// import Breadcrumbs from '@components/breadcrumbs'
//import LiveMatches from './livematches'
import { TrendingDown, ArrowUpRight } from 'react-feather'
//import Traffic from './Traffic'
//import TableBasic from './TableBasic'
//import { ThemeColors } from '@src/utility/context/ThemeColors'
// import Contests from './contests'
import {  getLiveMatchesList } from '../store/action'
//import StatsCard from './StatsCards'
import StatsVertical from '@components/widgets/stats/StatsVertical'
const StatisticsCards = () => {
  //const context = useContext(ThemeColors)
  const store = useSelector((state) => state.dashboard)
  const { liveMatchesLists } = store
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getLiveMatchesList())
    
  }, [dispatch])

  return (
    <Fragment>
      <Row>
      <Col  lg="4" xs="6" >
        <CardTitle tag="h4">Live Matches</CardTitle>
        <Row className="mt-0"> 
           <Col lg="4" xs="6">
            <StatsVertical
              icon={<ArrowUpRight size={21} />}
              color="success"
              stats={liveMatchesLists.today_live_matches}
              statTitle="Today"
            />
          </Col>
          <Col lg="4" xs="6">
            <StatsVertical
              icon={<TrendingDown size={21} />}
              color="danger"
              stats={liveMatchesLists.this_week_live_matches}
              statTitle="This Week"
            />
          </Col>
          <Col lg="4" xs="6">
            <StatsVertical
              icon={<ArrowUpRight size={21} />}
              color="primary"
              stats={liveMatchesLists.this_month_live_matches}
              statTitle="This Month"
            />
          </Col>
           </Row>
        </Col>
        {/* <Col lg="8" sm="12">
          <LiveMatches cols={{ md: '3', sm: '6' }} />
        </Col> */}
        
      </Row>
    
    </Fragment>
  )
}

export default StatisticsCards
